@import "_colors";

html,
body {
  padding: 0;
  margin: 0;
}
body {
  background: #fff;
  font-family: Helvetica;
  font-size: 16px;
}
#solidarischForm {
  position: absolute;
  top: 40px;
  left: 40px;
  width: 250px;
  @media screen and (max-width: 699px){
    position: static;
    margin:0 auto;
  }
}
.form {
  background-color: $white;
  border-radius: 2px;
  h3,
  small,
  p {
    color: $color;
  }
  .row {
    margin-bottom: 0;
  }
  input[type=text],
  input[type=email],
  input[type=passwort] {
    height: 2.5rem;
    border-bottom-color: $color;
    &:focus {
      border-bottom-color: $color;
      box-shadow: 0 1px 0 $color;
      + label {
        color: $color;
      }
    }
    &.valid {
      border-bottom: 1px solid $green;
      box-shadow: 0 1px 0 0 $green;
      + label {
        color: $green !important;
      }
    }
  }

  label {
    color: $color;
  }
  div.error {
    position: absolute;
    font-size: 0.7em;
    top: 1em;
    left: 100%;
    min-width: 220px;
    background: $color;
    color: $white !important;
    padding: 3px;
    box-shadow: 2px 2px 5px rgba(0,0,0,0.2);
    &::before {
      content: '';
      width: 0;
      height: 0;
      border-top: 11px solid transparent;
      border-bottom: 11px solid transparent;
      border-right: 11px solid $color;
      position: absolute;
      left: -11px;
      top: 0;
    }
  }
  .btn {
    margin-top: 1em;
    padding: 0 0.5em;
    width: 100%;
    border: 3px solid $color;
    background-color: #fff;
    color: $color;
    width: 100%;
    cursor: pointer;
    height: auto;
    min-height: 48px;
    &[disabled] {
      border: 3px solid #DFDFDF;
    }
  }
}
.sweet-alert {
  border-radius: 2px;
  button {
    border-radius: 2px;
  }
}
